*{
  margin: 5px;
}

.App{
  display: flex;
}

.first{
  width: 50%;
}


.second{
  width: 50%;
}

/* max < 1000 */
@media only screen and (max-width: 720px) {
  .App{
    display: grid;
  }
  img{
    width: 150px;
    height: 150px;
  }
  .about{
    margin-right: 20px;
    width: 100vw;
    margin: -7px;
  }
  .details{
    width: 100vw;
  }

  .text1{
    width: 90% !important;
  }
}
/* min > 1000 */
@media only screen and (min-width: 1000px) {
  .App{
    display: flex;
  }
  img{
    width: 150px;
    height: 150px;
  }

  .first {
    width: 90%;
  }
  .second {
    width: 90%;
  }

  
}